// About content
.about_container {
    margin: auto 12.5%;
    display: flex;
    flex-direction: column;
    gap: 3rem;

    .about_main_title {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;

        h2 {
            margin: 0;
            color: var(--app-primary-color);
            font-size: 2.5rem;
            text-transform: capitalize;
            letter-spacing: .25rem;
            position: relative;
            transition: all .2s ease-in-out;

            &::after {
                content: '';
                position: absolute;
                bottom: -50%;
                left: 50%;
                width: 35%;
                height: 5%;
                translate: -50% 0;
                background-color: var(--app-secondary-color);
                border-radius: 10rem;
            }
        }
    }

    .about_content {
        display: flex;
        gap: 4rem;
        padding-top: 2rem;

        .about_description {
            display: flex;
            flex-direction: column;
            gap: 3rem;
            flex: 1;

            .about_description_text {
                display: flex;
                flex-direction: column;
                gap: .8rem;

                p {
                    margin: 0;
                    line-height: 1.75rem;
                    letter-spacing: .025rem;
                    font-size: 1.15rem;
                    color: var(--app-intermediate-color);
                    transition: all .2s ease-in-out;

                    span {
                        font-weight: 800;
                        font-size: 1.1rem;
                        color: var(--app-primary-color);
                        padding: 0 .05rem;
                        transition: all .2s ease-in-out;
                    }

                    a {
                        color: var(--app-special-support-color2);
                    }
                }
            }

            .about_description_items_container {
                display: flex;
                flex-wrap: wrap-reverse;
                justify-content: center;
                gap: 1.35rem;

                .about_description_item {
                    border: .1rem solid var(--app-intermediate-color);
                    border-radius: 1rem;
                    transition: all .2s ease;
                    box-shadow: .075rem .15rem 1rem var(--app-intermediate-color-transparent2);
                    width: 29.5%;
                    transition: all .2s ease-in-out;

                    &:hover {
                        cursor: pointer;
                        translate: 0 -5%;
                        box-shadow: .075rem .25px 1rem var(--app-intermediate-color);
                    }

                    .description_item_text {
                        padding: 1rem .75rem;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: space-evenly;
                        text-align: center;
                        gap: .5rem;
                        text-decoration: none;

                        .description_item_text_title {
                            margin: 0;
                            font-size: 1.4rem;
                            font-weight: 800;
                            letter-spacing: .0425rem;
                            color: var(--app-primary-color);
                            transition: all .2s ease-in-out;
                        }

                        .description_item_text_explanation {
                            margin: 0;
                            position: relative;
                            text-transform: capitalize;
                            font-size: 1.05rem;
                            color: var(--app-secondary-support-color);
                            letter-spacing: .025rem;
                            transition: all .2s ease-in-out;
                        }
                    }
                }
            }

            .about_description_btn {
                display: flex;
                justify-content: flex-start;
                padding: 0;
                border-radius: 2rem;
                color: var(--app-primary-color);
                background-color: inherit;
                font-weight: 600;
                font-size: 1.1rem;
                text-decoration: none;
                position: relative;
                border: .1rem solid var(--app-secondary-support-color);
                align-self: flex-start;
                align-items: center;
                overflow: hidden;
                cursor: pointer;
                transition: all .2s ease-in-out;

                .btn_text {
                    padding: 0 .5rem 0 2rem;
                    word-spacing: .25rem;
                    z-index: 1;
                }

                .btn_icon {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    padding: .8rem 1.35rem .8rem .85rem;
                    z-index: 1;
                }

                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 100%;
                    height: 100%;
                    transform: translateX(-100%);
                    transition: all .3s ease-out;
                    z-index: 0;
                }

                &:hover {
                    &::before {
                        width: 100%;
                        height: 100%;
                        background-color: var(--app-intermediate-color-transparent);
                        transform: translateX(0%);
                    }
                }

            }
        }

        .about_skills {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            // gap: 1.5rem;
            gap: 1.25rem;
            flex: 1;
            //width: 40%;

            .about_skills_title {
                margin-top: 0;
                font-size: 1.75rem;
                letter-spacing: .075rem;
                word-spacing: .1rem;
                text-align: center;
                color: var(--app-primary-color);
                transition: all .2s ease-in-out;
            }

            .about_skills_technologies {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                gap: 1.75rem;

                .skills_technology_container {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: .5rem;

                    p {
                        margin: 0;
                        letter-spacing: .05rem;
                        font-weight: bold;
                        color: var(--app-secondary-support-color2);
                        font-size: .9rem;
                    }

                    img {
                        height: 46px;
                        border-radius: .5rem;

                        &.skill_img_rounded_border {
                            border-radius: 50%;
                        }
                    }

                    &:nth-of-type(20) {
                        filter: contrast(65%);
                    }

                    &:nth-of-type(6),
                    &:nth-of-type(11) {
                        filter: contrast(80%);
                    }
                }
            }
        }
    }

    .about_timeline_container {
        display: flex;
        flex-direction: column;
        gap: .75rem;

        .about_timeline_title {
            margin: 2.25rem 0 1.5rem 0;
            font-size: 1.75rem;
            letter-spacing: .075rem;
            word-spacing: .1rem;
            text-align: center;
            color: var(--app-primary-color);
            transition: all .2s ease-in-out;
        }

        .about_timeline_row {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: max(10%, 100px);
            row-gap: 1.5rem;

            .about_timeline_column {
                display: flex;
                flex-direction: column;
                gap: 2.75rem;
                flex: 1 1 350px;

                h4.about_timeline_subtitle {
                    font-size: 1.6rem;
                    letter-spacing: .05rem;
                    word-spacing: .1rem;
                    text-align: center;
                    margin: 0;
                    color: var(--app-intermediate-color);
                    transition: all .2s ease-in-out;
                }

                .about_timeline_content {
                    display: flex;
                    flex-direction: column;
                    gap: 0;
                    padding-bottom: 2rem;

                    .about_timeline_item {
                        display: flex;
                        flex-direction: column;
                        gap: 1.2rem;
                        position: relative;
                        padding-left: 2.5rem;
                        border-left: .05rem solid var(--app-secondary-color);
                        border-radius: .25rem;
                        padding-bottom: 3.25rem;
                        transition: all .2s ease-in-out;

                        .timeline_icon {
                            position: absolute;
                            top: 0;
                            left: -1rem;
                            border: .1rem solid var(--app-primary-color);
                            background-color: var(--app-bg-color);
                            width: 2rem;
                            height: 2rem;
                            border-radius: 50%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            transition: all .2s ease-in-out;

                            i {
                                font-size: 1.1rem;
                                color: var(--app-primary-color);
                                transition: all .2s ease-in-out;
                            }
                        }

                        .timeline_duration {
                            margin: 0;
                            padding: .25rem 1rem;
                            color: var(--app-secondary-support-color2);
                            border: .1rem solid var(--app-secondary-support-color2);
                            border-radius: 2rem;
                            //display: inline-block;
                            font-size: 1rem;
                            font-weight: 800;
                            width: fit-content;
                            transition: all .2s ease-in-out;
                        }

                        h5 {
                            gap: .85rem;
                            margin: 0;
                            letter-spacing: .075rem;
                            color: var(--app-primary-color);
                            font-size: 1.45rem;
                            font-weight: 800;
                            transition: all .2s ease-in-out;

                            span {
                                color: var(--app-secondary-support-color2);
                                padding-left: .2rem;
                                font-weight: 500;
                                font-size: 1.2rem;
                                letter-spacing: .05rem;
                                transition: all .2s ease-in-out;

                                &.flex {
                                    display: flex;
                                    padding-left: unset;
                                    padding-top: .1rem;
                                }
                            }
                        }

                        ul {
                            padding-left: .5rem;
                            display: flex;
                            flex-direction: column;
                            gap: .55rem;

                            li {
                                line-height: 1.4rem;
                            }
                        }

                        .timeline_description {
                            margin: 0;
                            color: var(--app-intermediate-color);
                            font-size: 1.05rem;
                            letter-spacing: .035rem;
                            line-height: 1.5rem;
                            transition: all .2s ease-in-out;

                            span {
                                color: var(--app-special-support-color);
                                font-weight: 800;
                                padding: 0 .05rem;
                                transition: all .2s ease-in-out;
                            }
                        }
                    }
                }
            }
        }


    }
}

@import './media';